body {
  background: #ebebeb; }

.site-container {
  max-width: 800px;
  margin: 0 auto; }
  .site-container__inner {
    padding: 20px; }

.site-header__title {
  margin: 1.5rem 0;
  font-size: 48px;
  font-family: "Fira Sans", sans-serif;
  font-weight: 700;
  color: #111; }

.site-intro {
  margin-bottom: 80px; }
  .site-intro__content > p {
    margin: 0;
    font-size: 24px;
    font-family: "Montserrat", sans-serif;
    line-height: 1.4;
    color: #414141; }
  .site-intro__content > p + p {
    margin-top: 1rem; }

.site-footer__copyright {
  font-size: 16px;
  font-family: "Montserrat", sans-serif;
  line-height: 1.4;
  color: #414141;
  text-align: center; }
  .site-footer__copyright > a {
    color: #F03434;
    text-decoration: none; }

.articles {
  padding: 0 60px; }

.article {
  padding: 40px;
  background: #fff;
  border-radius: 3px;
  box-shadow: 0 3px 2px 0 rgba(0, 0, 0, 0.1), 0 0 2px 2px rgba(0, 0, 0, 0.1); }
  .article__header {
    margin: 0 0 20px 0;
    color: #F03434; }
    .article__header::before {
      content: "";
      display: block;
      width: 50px;
      height: 4px;
      margin: 0 0 20px 0;
      background-color: #F03434; }
  .article__title {
    margin: 0;
    font-size: 36px;
    font-family: "Fira Sans", sans-serif;
    font-weight: 700; }
  .article__date {
    font-size: 16px;
    font-family: "Montserrat", sans-serif;
    color: #414141; }
  .article__content > h3 {
    margin: 2rem 0 0.75rem 0;
    font-size: 28px;
    font-family: "Fira Sans", sans-serif;
    font-weight: 700;
    color: #111; }
  .article__content > h4 {
    margin: 2rem 0 0.5rem 0;
    font-size: 20px;
    font-family: "Fira Sans", sans-serif;
    font-weight: 700;
    color: #111; }
  .article__content > p {
    margin: 0;
    font-size: 20px;
    font-family: "Montserrat", sans-serif;
    line-height: 1.4;
    color: #414141; }
  .article__content > p + p {
    margin-top: 1.5rem; }
  .article__content > p > img {
    display: block;
    max-width: 100%;
    height: auto; }
  .article__content > p a {
    display: inline-block;
    color: #F03434;
    text-decoration: none;
    border-bottom: 2px solid;
    padding: 0.1em 0.2em 0;
    margin: 0 -0.1em 1em;
    position: relative; }
    .article__content > p a::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: currentColor;
      opacity: 0;
      transition: opacity 0.2s; }
    .article__content > p a:hover::before {
      opacity: 0.1; }
    .article__content > p a:active::before {
      opacity: 0.2;
      transition-duration: 0s; }
  .article__content > ul {
    margin: 1.5rem 0;
    padding: 0 0 0 3rem; }
    .article__content > ul li {
      font-size: 20px;
      font-family: "Montserrat", sans-serif;
      line-height: 1.4;
      color: #414141; }
  .article + .article {
    margin-top: 40px; }

.pagination {
  margin: 40px 0 80px; }
  .pagination__count {
    display: block;
    font-size: 20px;
    font-family: "Montserrat", sans-serif;
    line-height: 1.4;
    color: #414141;
    text-align: center; }
    .pagination__count::before {
      content: "";
      display: block;
      width: 50px;
      height: 4px;
      margin: 0 auto 20px;
      background-color: #F03434; }
